import * as React from 'react';
import { LinkProps } from 'react-router-dom';

import { TCustomer } from './types';
import { TCustomerInstance } from './models';
import Staff from '@miq/staffjs';
import { getCN } from '@miq/utiljs';

type TCustomerDataOrInstance = TCustomer | TCustomerInstance;

export const CustomerUpdateLink = ({ data, ...props }: { data: TCustomerDataOrInstance } & Omit<LinkProps, 'to'>) => (
  <Staff.Link
    {...props}
    to={`/staff/sales/customers/${data?.slug}/`}
    requiredPerms={['sales.change_customer']}
    className={getCN(['customer-update-link', props.className])}
    title="Update customer"
  />
);
