import * as React from 'react';

import { useDocumentTitle, useRequest } from '@miq/hookjs';
import Staff from '@miq/staffjs';
import { Response, uuid } from '@miq/utiljs';
import { Icons, Loading, ViewSection } from '@miq/componentjs';
import { TCustomer } from './types';
import { Customer, TCustomerInstance } from './models';
import { CustomerUpdateLink } from './links';
import { CustomerCard } from './components';

export default function CustomerListView() {
  useDocumentTitle('Customers - Sales');
  const { res, loading, refresh, ...request } = useRequest(() => Customer().list());

  if (loading) return <Loading />;

  const r = new Response<TCustomer>(res);
  if (!r.isSuccess) return null;

  return (
    <Staff.View className="">
      <div className="d-grid">
        {r.items.map((data) => (
          <CustomerListItem data={data} key={uuid()} />
        ))}
      </div>
    </Staff.View>
  );
}

const CustomerListItem = ({ data }: { data: TCustomer | TCustomerInstance }) => {
  const cust = Customer(data);
  return (
    <ViewSection
      border
      title={<CustomerCard data={cust.data} />}
      actions={
        <>
          <CustomerUpdateLink data={data} className="btn btn-primary-3 btn-square">
            <Icons.Edit />
          </CustomerUpdateLink>
        </>
      }
      className="cust-list-item"
    />
  );
};
